<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <div class="demonstration-box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from 'views/common/options'
import Tables from 'views/common/tables'
import queryString from "querystring"
import page from 'data/page'

const dataOption = {
  leftMenu: [
    { id:'1', name:'校区：', placeholder:'请输入校区', module:'name'},
    { id:'2', name:'班级：', placeholder:'请输入班级', module:'count'},
    { id:'3', name:'带班老师：', placeholder:'请输入带班老师', module:'price'},
    { id:'4', name:'学员姓名：', placeholder:'请输入学员姓名', module:'count'},
    { id:'5', name:'学员手机：', placeholder:'请输入学员手机', module:'count'},
    { id:'6', name:'学员积分：', placeholder:'请输入学员积分', module:'count'},
  ],
  rightMenu: [
    { id:'r1', name:'查询'},
    { id:'r2', name:'新增'}
  ]
}

const dataTable = {
  table: [
    { label:'校区', prop:'name'},
    { label:'班级', prop:'count'},
    { label:'带班老师', prop:'price'},
    { label:'学员姓名', prop:'studentName'},
    { label:'学员手机', prop:'mobile'},
    { label:'学员积分', prop:'integral'},
  ],
  edit: [
    {name: '积分明细', id: 't3'},      
    {name: '编辑', id: 't1'},
    {name: '删除', id: 't2'},
  ]
}

const tips = ['确认删除该条信息?'];

export default {
  name: 'studentList',
  mixins: [page],
  data() {
    return {
      dataOption: dataOption,
      dataTable: dataTable,
      dialogVisible: false,
      dialogContent: tips[0],
      tableList: [],
      loading: false,
      currentPage: 1,
      id: ''
    }
  },
  components: {
    Options,
    Tables
  },
  mounted(){
    this.search();
  },
  methods:{
    optchange(params){
      console.log(params.id + '--id被单击了')
      if(params.id === 'r1') return this.search();
      if(params.id === 'r2') return this.$router.push(`/student/add?type=new`);
    },
    tabchange(params){
      console.log(params.id + '--id被单击了')

      this.id = params.data.id;
      // 编辑
      if(params.id === 't1') return this.$router.push(`/student/add?type=edit&id=${params.id}`);
      // 删除
      if(params.id === 't2') return this.dialogVisible = true;
      // 积分明细
      if(params.id === 't3') return this.$router.push(`/student/detail?type=detail&id=${params.id}`);
    },
    sure(){
      this.search();
      this.dialogVisible = false;
    },
    search(){  //搜索
      const optParams = this.$refs.goodsOpt.getData();
      const pageParams = { size: 20, page: this.currentPage -1 };

      const params = Object.assign({}, optParams, pageParams)
      this.tableList = [
        {name: '中山校区', count: '三年级二班', price: '王老师', studentName: '张学生', mobile:'18745213652', integral: '1000'},
        {name: '中山校区', count: '三年级二班', price: '王老师', studentName: '张学生', mobile:'18745213652', integral: '1000'},
        {name: '中山校区', count: '三年级二班', price: '王老师', studentName: '张学生', mobile:'18745213652', integral: '1000'},
      ]
      // this.loading = true;
      // const param = queryString.stringify(params || {});
      // let url = urls[code];
      // this.api.get(`/role`).then((res)=>{
        // this.loading = false;
        // this.data = res.data.list;
        // this.setPage(res.data)
        // this.dataInit();
      // }).catch((err) => {
      //   console.log(err)
      //   this.$message(err.message)
      // })
    },
    dataInit(){//数据初始化

    }
  }
}
</script>
 
